<template>
  <div class="about">
    <title-background class="about-title" :color="c">
      <h1>About me</h1>
    </title-background>
  </div>
</template>
<script>
import TitleBackground from "@/components/TitleBackground.vue";
import {defineComponent} from "vue";

export default defineComponent({
  components: {TitleBackground}
})

</script>

<script setup>
import {onMounted, ref} from "vue";

let c = "#a23d52 48deg, #e79f53 85deg, #d44a75 345deg"
let l = ref('-15px')
let b = ref('-25px')
let tc = ref('-25px')
function f() {
  if (scrollY > 5) {
    l.value = '-30px'
    b.value = '-60px'
    tc.value = 'rgba(248,249,250,0.35)'
  } else {
    l.value = '-15px'
    b.value= '-25px'
    tc.value = '#f8f9fa'
  }
}

onMounted(() => {
  addEventListener('scroll',f)
})
// console.log(2 + c)
</script>

<style lang="scss">
.about-title {
  width: 100%;
  height: 65vh;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  position: relative;

  h1 {
    position: absolute;
    bottom: v-bind(b);
    left: v-bind(l);
    font-size: 12em;
    color: v-bind(tc);
    transition: all 0.15s ease-in-out;
  }
}
</style>